@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&family=STIX+Two+Text:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&family=Dancing+Script:wght@500&family=Lobster&family=Roboto&family=STIX+Two+Text:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&family=Dancing+Script:wght@500&family=Lobster&family=Roboto&family=Russo+One&family=STIX+Two+Text:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.video {
    width: 100%;
}
.main {
    position: relative;
}
.content {
    position: absolute;
    right: 0;
    top: 400px;
    color: #e8e8e8;
    width: 560px;
    font-weight: 700;
    padding: 20px;
    text-align: center;
    font-family: 'Russo One', sans-serif;
    font-size: 60px;
}
.menuText {
    color: #404040;
    font-family: 'STIX Two Text', serif;
}

@media screen and (max-width: 1100px) {
    .content {
        font-size: 40px;
        top: 380px;
        width: 350px;
        padding: 0;
    }
}

@media screen and (max-width: 770px) {
    .content {
        font-size: 25px;
        top: 110px;
        width: 205px;
        padding: 0;
    }
}
@media screen and (max-width: 400px) {
    .content {
        font-size: 20px;
        top: 100px;
        width: 205px;
        padding: 0;
    }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) and (max-width: 1199px) {
    .content {
        top: 300px;
    }
    #about .photos {
        width: 85%;
    }
    #about .photos .we {
        width: 340px;
    }
    .white .otherPhoto {
        width: 420px;
    }
    .white .buket {
        width: 330px;
    }
    .white p {
        font-size: 20px !important;
    }
    .counterContainer {
        width: 70% !important;
    }
    .time {
        padding: 15px 28px !important;
        font-size: 30px !important;
    }
    .time span {
        font-size: 15px !important;
    }
    .location p {
        font-size: 17px !important;
    }
    .castleIcon {
        width: 35px !important;
    }
    .white #map {
        width: 400px !important;
    }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
    .content {
        top: 300px;
    }
    #about .photos {
        width: 80%;
    }
    #about .photos .we {
        width: 380px;
    }
    .time {
        padding: 15px 28px;
    }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) and (max-width: 991px) {
    .content {
        top: 260px;
    }
    #about .photos {
        width: 90%;
    }
    #about .photos .we {
        width: 280px;
    }
    .aboutUs {
        width: 100%;
    }
    .white {
        flex-direction: column !important;
        padding: 20px 20px 20px !important;
    }
    .white p {
        width: auto !important;
        border-right: none !important;
        border-bottom: 1px solid grey !important;
        margin: 0 !important;
        padding: 0 !important;
    }
    #borderLeft {
        border-left: none !important;
        margin: 0 !important;
        padding: 0 !important;
    }
    .otherPhoto {
        width: 545px !important;
    }
    .otherPhoto.buket {
        width: 310px !important;
    }
    #column {
        font-size: 20px !important;
    }
    .counterContainer {
        width: 90% !important;
    }
    .time {
        padding: 15px 28px !important;
        font-size: 30px !important;
    }
    .time span {
        font-size: 15px !important;
    }
    .location p {
        font-size: 19px !important;
        margin-bottom: 15px !important;
    }
    .castleIcon {
        width: 35px !important;
    }
    .white #map {
        width: 400px !important;
    }
    #reverse {
        flex-direction: column-reverse !important;
    }
}

